/* Ember supports plain CSS out of the box. More info: https://cli.emberjs.com/release/advanced-use/stylesheets/ */

@import url('https://fonts.googleapis.com/css2?family=Ubuntu:ital,wght@0,300;0,400;0,500;0,700;1,300;1,400;1,500;1,700&display=swap');
*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: "Ubuntu";
    font-weight: lighter;
}
body{
    background: #511845;
    color: white;
}

$primary-color:#e95420;


.read-button{
    background-color: $primary-color;
    width: fit-content;
    padding: 10px 15px;
    border-radius: 8px;
    color: white;
    font-weight: 500;
    cursor: pointer;

}

//Navigation bar styles 

.navigationBar-Container{
    height: 98vh;
    width: 50px;
    background-color: #fffdfa3a ;
    position: fixed;
    z-index: 3;
    margin: 0px 9px;
    border-radius: 8px;
    top: 8px;
    backdrop-filter: blur(150px);
}
.homepage-container{
    display: flex;
    max-width: 100vw;
    background:  #8b3a4a1e ;
    backdrop-filter: blur(150px);
    position: relative;
    z-index: 1;
    height: fit-content;
 
    .linux-unix-switch{
        position: absolute;
        right: 5%;
        display: flex;
        border: 1px solid $primary-color;
        width: 200px;
        padding: 4px 0;
        top: 10px;
        z-index: 5;
        justify-content: space-around;
        border-radius: 50px;
        .linux-unix-btn{
          width: 90px;
          border-radius: 50px;
          height: 30px;
          text-align: center;
          line-height: 30px;
          font-weight: 500;
          cursor: pointer;
          color: white;
          &.active{
            background: $primary-color;
      
          }
        }
    }

}
.welcome-box{
    width:90vw;
    height: 400px;
    background: rgba(255, 255, 255, 0.199);
    // border: 1px solid rgba(233, 84, 32, 0.2) ;
    backdrop-filter: blur(150px);
    border-radius: 15px;
    margin:  auto;
    margin-top: 60px;
    color: white;
  .images-box{
    img{
        margin-top: 250px;
    }
  }
  .content-box{
    font-size: 18px;
    font-weight: 500;
    padding-left: 60px;
    color: rgba(255, 255, 255, 0.911);
    max-width: 50%;
    .read-button{
        margin-right: 60px;
        margin-top: 20px;
    }
  }

}
.blogs-tiles-container{
    width:99vw;
    margin: auto;
    margin-top: 20px;
    padding-top: 100px;
    padding-bottom: 50px;
    padding-left: 50px;

}
.blog-tile{
    height: 400px;
    width: 400px;
    cursor: pointer;
    overflow: hidden;
    position: relative;
    z-index: 5;
    margin: 15px;
    margin-right: 20px;
    .blog-title{
        font-size: 22px;
        font-weight: 500;
        padding: 10px;
    }
    img{
    border-radius: 12px;
    height: 240px;
    }
}
